<template>
       <v-col cols="12" xs="12" sm="10" md="8" lg="8" class="mx-auto">
           <v-breadcrumbs :items="items">
               <template v-slot:divider>
                   <v-icon>mdi-chevron-right</v-icon>
               </template>
           </v-breadcrumbs>
           <v-card class="__card">
               <v-card-title class="__card-title justify-center">
                   <p> ອັບເດດ Portfolio</p>
               </v-card-title>
               <hr />
               <v-col cols="10" class=" mx-auto">
                   <ValidationObserver ref="observer" v-slot="{ invalid }" slim>
                       <v-form @submit.prevent="updatePortfolio" class="text-center">
                           <h2 class="font-weight-bold">ຂໍ້ມູນຂອງ Portfolio</h2>
                           <div class="uploader mb-5"
                                @dragenter="OnDragEnter"
                                @dragleave="OnDragLeave"
                                @dragover.prevent
                                @drop="onDrop"
                                :class="{ dragging: isDragging }">
                               <i class="fas fa-cloud-upload-alt fa-3x"></i>
                               <p>ລາກຮູບມາໄວ້ບ່ອນນີ້...</p>
                               <div>ຫຼື</div>
                               <div class="file-input">
                                   <label for="file">ເລືອກຮູບ</label>
                                   <input @change="onInputChange" type="file" id="file" multiple>
                               </div>
                               <v-row>
                                   <v-col cols="4" xs="4" sm="4" md="3" lg="3" v-for="(image, index) in imgs" :key="index">
                                       <v-img
                                               width="90" height="90"
                                               class="text-right"
                                               :src="$server + image"
                                       >
                                           <i  @click="spliceImgs(image, index)" class="fas fa-backspace mr-1 red--text i"></i>
                                       </v-img>
                                       <div class="detail">
                                           <!--                    <span class="name" v-text="files[index].name"></span>-->
                                           <!--                    <span class="size" v-text="files[index].size"></span>-->
                                       </div>
                                   </v-col>
                                   <v-col cols="4" xs="4" sm="4" md="3" lg="3" v-for="(image, index) in images" :key="index">
                                       <v-img
                                               width="90" height="90"
                                               class="text-right"
                                               :src="image"
                                       >
                                           <i @click="spliceImages(index)"
                                              class="fas fa-backspace mr-1 red--text i"></i>
                                       </v-img>
                                       <div class="detail">
                                           <!--                    <span class="name" v-text="files[index].name"></span>-->
                                           <!--                    <span class="size" v-text="files[index].size"></span>-->
                                       </div>
                                   </v-col>
                               </v-row>
                           </div>
                           <ValidationProvider
                                   mode="lazy"
                                   name="ຫົວຂໍ້"
                                   rules="required|between: 4, 50"
                                   v-slot="{ errors }"
                           >
                               <v-text-field
                                       v-model="portfolio.title"
                                       label="ຫົວຂໍ້"
                                       outlined
                                       clearable
                                       :error-messages="errors"
                               ></v-text-field>
                           </ValidationProvider>
                           <ValidationProvider
                                   mode="lazy"
                                   name="ລາຍລະອຽດ"
                                   rules="required|between: 10, 2000"
                                   v-slot="{ errors }"
                           >
                               <ckeditor :editor="editor" v-model="portfolio.description" ></ckeditor>
                               <p v-if="errors" class="error--text">{{ errors[0] }}</p>
                           </ValidationProvider>
                           <ValidationProvider
                                   mode="lazy"
                                   name="ປະເພດ"
                                   rules="required"
                                   v-slot="{ errors }"
                           >
                               <v-select
                                       v-model="portfolio.categoryId._id"
                                       :items="categories"
                                       item-text="name"
                                       item-value="_id"
                                       :menu-props="{ top: true, offsetY: true }"
                                       label="ປະເພດ"
                                       outlined
                                       :error-messages="errors"
                               ></v-select>
                           </ValidationProvider>
                           <v-btn type="submit" class="v-size--x-large success">
                               ອັບເດດ Portfolio
                           </v-btn>
                       </v-form>
                   </ValidationObserver>
               </v-col>
           </v-card>
       </v-col>
</template>

<script>
    import { mapGetters } from 'vuex'
    import { ValidationProvider, extend } from 'vee-validate';
    import { required } from 'vee-validate/dist/rules';
    import { ValidationObserver } from 'vee-validate'
    import Vue from 'vue';
    import CKEditor from '@ckeditor/ckeditor5-vue';
    import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
    Vue.use( CKEditor );
    extend('required', {
        ...required,
        message: 'ກະລຸນາປ້ອນຂໍ້ມູນໃນ {_field_}'
    })
    extend('between', {
        validate: (value, { min, max }) => {
            const length = value && value.length;
            return length >= min && length <= max;
        },
        params: ['min', 'max'],
        message: 'ຂໍ້ມູນ {_field_} ຕ້ອງມີຄວາມຍາວລະຫວ່າງ {min} ຫາ {max} ຕົວອັກສອນ'
    })
    export default {
        components: {
          ValidationProvider,
          ValidationObserver
        },
        data: () => ({
            editor: ClassicEditor,
            portfolio: {
                title: '',
                description: '',
                categoryId: ''
            },
            categories: [],
            //
            imgs: [],
            imgPath: [],
            //
            files: [],
            images: [],
            isDragging: false,
            dragCount: 0,
            // breed
            items: [
                {
                    text: 'Dashboard',
                    to: { name: 'Dashboard' },
                    exact: true
                },
                {
                    text: 'Post portfolio',
                }
            ]
        }),
        computed: {
            ...mapGetters([
                'auth'
            ]),
            // dragLeave () {
            //     if(this.images.length <= 0){
            //         this.isDragging = false
            //     }
            //     if(this.images.length >= 1) {
            //         this.isDragging = true
            //     }
            // }
        },
        methods: {
            OnDragEnter(e) {
                e.preventDefault()
                this.dragCount++
                this.isDragging = true
            },
            OnDragLeave(e) {
                e.preventDefault()
                this.dragCount--
                if(this.dragCount <= 0)
                    this.isDragging = false
            },
            onInputChange(e) {
                const files = e.target.files
                Array.from(files).forEach(file => this.addImage(file))
            },
            onDrop(e){
                e.preventDefault()
                e.stopPropagation()
                const files = e.dataTransfer.files
                Array.from(files).forEach(file => this.addImage(file))
            },
            addImage(file) {
                if(!file.type.match('image.*')){
                    this.$swal({
                        title: 'File ຕ້ອງເປັນຮູບພາບ',
                        type: 'error',
                        toast: true,
                        position: 'top-end',
                        showConfirmButton: false,
                        timer: 2000,
                    })
                    return
                }
                this.files.push(file)
                const reader = new FileReader()
                reader.onload = (e) => this.images.push(e.target.result)
                reader.readAsDataURL(file)
            },
            async fetchOne () {
               const results = await this.$axios.get('portfolio/getone' , { params: { _id: this.$route.params.id } })
               const portfolio = results.data.portfolio
                this.portfolio = portfolio
                this.imgs = portfolio.images
            },
            async fetchCategories () {
                const results = await this.$axios.get('category/getall')
                const categories = results.data.categories
                this.categories = categories
            },
            async updatePortfolio () {
                try {
                    const isValid = await this.$refs.observer.validate()
                    if(isValid) {
                        const formData = new FormData()
                        const port = this.portfolio
                        formData.append('title', port.title)
                        formData.append('description', port.description)
                        formData.append('categoryId', port.categoryId._id)
                        this.imgs.forEach(image => {
                            formData.append('images[]', image)
                        })
                        this.imgPath.forEach(path=>{
                            formData.append('imgPath[]', path) // array because single image
                        })
                        this.files.forEach(file=>{
                            formData.append('images', file) // not array because backend is array
                        })
                        await this.$axios.put('portfolio/update', formData, { params: { _id: this.$route.params.id } })
                        this.$router.push({ name: 'Dashboard' })
                        this.$swal({
                            title: 'ອັບເດດ Portfolio ແລ້ວ',
                            type: 'success',
                            toast: true,
                            position: 'top-end',
                            showConfirmButton: false,
                            timer: 2000,
                        })
                        this.$io.emit('updatePortfolio', this.portfolio._id)
                    }
                } catch (e) {
                    throw e
                }
            },
            spliceImgs(path, index) {
                let length = this.imgs.length + this.images.length
                if(length > 1) {
                    this.imgPath.push(path)
                    this.imgs.splice(index, 1)
                }else {
                    this.$swal({
                        title: 'ກະລຸນາເພີ່ມຮູບໃໝ່ກ່ອນ ຈຶ່ງຈະສາມາດລົບຮູບນີ້ໄດ້',
                        type: 'error',
                        toast: true,
                        position: 'top-end',
                        showConfirmButton: false,
                        timer: 2000,
                    })
                }
            },
            spliceImages(index) {
                let length = this.imgs.length + this.images.length
                if (length > 1) {
                    this.files.splice(index, 1)
                    this.images.splice(index, 1)
                }else {
                    this.$swal({
                        title: 'ກະລຸນາເພີ່ມຮູບໃໝ່ກ່ອນ ຈຶ່ງຈະສາມາດລົບຮູບນີ້ໄດ້',
                        type: 'error',
                        toast: true,
                        position: 'top-end',
                        showConfirmButton: false,
                        timer: 2000,
                    })
                }
            }
        },
        created () {
            this.fetchOne()
            this.fetchCategories()
        }

    }
</script>

<style lang="scss" scoped>
    .__card {
        padding-bottom: 50px;
    }
    .__card-title {
        font-size: 30px;
        font-weight: bold;
    }
    .uploader {
        width: 100%;
        background: #2196F3;
        color: #fff;
        padding: 40px 15px;
        text-align: center;
        border-radius: 10px;
        border: 3px dashed #fff;
        font-size: 20px;
        position: relative;
        &.dragging {
            background: #fff;
            color: #2196F3;
            border: 3px dashed #2196F3;
            .file-input label {
                background: #2196F3;
                color: #fff
            }
        }
    }
    .file-input {
        width: 200px;
        margin: auto;
        height: 48px;
        position: relative;
        label, input {
            background: #fff;
            color: #2196F3;
            width: 100%;
            position: absolute;
            left: 0;
            top: 0;
            padding: 10px;
            border-radius: 4px;
            margin-top: 7px;
            cursor: pointer;
        }
        input {
            opacity: 0;
            z-index: -12;
        }
    }
    .i {
        &:hover {
            transform: scale(1.3);
        }
    }
    hr {
        margin: auto;
        width: 95%;
    }
</style>

